import React, { useState, useEffect } from "react";
// import { Link, Router } from "react-router-dom";
import getData from "../Datas/FetchData";
import phonne from "../staticfiles/imgs/C0.svg";
import mail from "../staticfiles/imgs/C1.svg";

function PrPer() {
  const [PaPer, PaPerF] = useState([]);

  useEffect(() => {
    let paper = [];
    getData().then((da) => {
      for (let a = 0; a < da.length; a++) {
        if (da[a].per !== undefined) {
          paper.push(da[a].per);
        }
      }
      PaPerF(paper);
    });
  }, []);
  // console.log(PaPer);
  return (
    <div className="CONTMAIN">
      <div className="PrPho">
        <img src={PaPer[0]} alt="Pramod" />
      </div>
      <div className="PrNam">
        <h3 className="TAL">{PaPer[1]}</h3>
      </div>
      <h2 className="LMA TAL">___________</h2>
      <div className="PrCon">
        <span className="CON BOSH C0">
          <a href={`tel:// ${PaPer[2]}`}>
            <img src={phonne} width="25" height="25" alt="Phone" />
          </a>
        </span>
        <span className="CON BOSH C1">
          <a href={`mailto:${PaPer[3]}`} target="_blank">
            <img src={mail} width="25" height="25" alt="Mail" />
          </a>
        </span>
      </div>
    </div>
  );
}

export default PrPer;
