import React, { useState, useEffect } from "react";
import getData from "../Datas/FetchData";

function PrSki() {
  const [PaSki, PaSkiF] = useState([]);

  useEffect(() => {
    let paski = [];
    getData().then((da) => {
      for (let a = 0; a < da.length; a++) {
        if (da[a].ski !== undefined) {
          paski.push(da[a].ski);
        }
      }
      PaSkiF(paski);
    });
  }, []);
  //   console.log(PaSki);
  const CalWid = () => {
    return PaSki.length * 151;
  };

  return (
    <div className="CONTMAIN">
      <h4>Skills:</h4>
      <div className="SKISCR">
        <div className="SKISHOBOARD" style={{ width: CalWid() }}>
          {PaSki.map((a, b) => (
            <span key={b}>
              <div className="SKIIMG">
                <img src={a.split("|")[1]} width="100" />
              </div>
              <div className="SKIPER"></div>
              <div className="SKINAM">
                <h5>{a.split("|")[0]}</h5>
              </div>
            </span>
          ))}
        </div>
      </div>
    </div>
  );
}

export default PrSki;
