const axios = require("axios");
//'https://docs.google.com/spreadsheets/d/13RomIsQ18QvhKVjwOMJtIl4_fJDXyb_h9Ig9uN5jIC4/edit?usp=sharing'
const li =
  //"https://spreadsheets.google.com/feeds/cells/1ICVn8TbCCn-FxC1GjxB2gdygsSp63IUbJs6at3C0QbQ/od6/public/basic?alt=json";
  "https://spreadsheets.google.com/feeds/cells/13RomIsQ18QvhKVjwOMJtIl4_fJDXyb_h9Ig9uN5jIC4/od6/public/basic?alt=json";

const getData = () => {
  return axios
    .get(li, { headers: { "Content-Type": "application / json" } })
    .then((data) => {
      let DaPra = [];
      data.data.feed.entry.map((m, mm) => {
        if (m.title.$t.charAt(0) === "A") {
          DaPra.push({ ["ski"]: m.content.$t });
        }
        if (m.title.$t.charAt(0) === "B") {
          DaPra.push({ ["per"]: m.content.$t });
        }
        if (m.title.$t.charAt(0) === "C") {
          DaPra.push({ ["ame"]: m.content.$t });
        }
        if (m.title.$t.charAt(0) === "D") {
          DaPra.push({ ["exp"]: m.content.$t });
        }
      });
      return DaPra;
    });
};

export default getData;
