import React, { Component } from "react";
import getData from "../Datas/FetchData";

class PrExp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      PaExp: [],
    };
  }

  componentDidMount() {
    let PaExp = [];
    getData().then((da) => {
      for (let a = 0; a < da.length; a++) {
        if (da[a].exp !== undefined) {
          PaExp.push(da[a].exp);
        }
      }
      this.setState({
        PaExp,
      });
    });
  }

  render() {
    return (
      <div className="CONTMAIN">
        <h4>Experience:</h4>
        <div className="EXPEER">
          <ul>
            {this.state.PaExp.map((e, ex) => (
              <li key={ex}>{e.split("|")[0]}</li>
            ))}
          </ul>
        </div>
      </div>
    );
  }
}

export default PrExp;
