import React, { useState, useEffect } from "react";
import getData from "../Datas/FetchData";

let PrAme = () => {
  const [PaAme, PaAmeF] = useState([]);

  useEffect(() => {
    let paame = [];
    getData().then((da) => {
      for (let a = 0; a < da.length; a++) {
        if (da[a].ame !== undefined) {
          paame.push(da[a].ame);
        }
      }
      PaAmeF(paame);
    });
  }, []);
  //   console.log(PaAme);
  return (
    <div className="CONTMAIN">
      <h4>About Me:</h4>
      {PaAme[0]}
    </div>
  );
};

export default PrAme;
